"use client";

import { useState, useEffect } from "react";
import { AlertDialog, AlertDialogTrigger, AlertDialogContent, AlertDialogHeader, AlertDialogFooter, AlertDialogTitle, AlertDialogDescription } from "@/components/ui/alert-dialog";
import { Button } from "@/components/ui/button";

type ItemMenu = {
  id: number;
  nomeProduto: string;
  descricao: string;
  preco: number;
  imagemUrl: string;
  tipo: "espeto" | "sobremesa" | "acompanhamento" | "bebidas";
};

const MenuPage = () => {
  const [mesa, setMesa] = useState<string>("");
  const [pedido, setPedido] = useState<ItemMenu[]>([]);
  const [produtos, setProdutos] = useState<ItemMenu[]>([]);
  const [categoriaSelecionada, setCategoriaSelecionada] = useState<"espeto" | "sobremesa" | "acompanhamento" | "bebidas">("espeto");
  const [showAlert, setShowAlert] = useState(false);

  useEffect(() => {
    const fetchProdutos = async () => {
      try {
        const response = await fetch("/pages/api/produto/[id]");
        if (!response.ok) throw new Error("Erro ao carregar produtos");
        const data: ItemMenu[] = await response.json();
        setProdutos(data);
      } catch (error) {
        console.error("Erro ao buscar produtos:", error);
      }
    };

    fetchProdutos();
  }, []);

  const adicionarAoPedido = (item: ItemMenu) => {
    setPedido((prev) => [...prev, item]);
  };

  const calcularTotal = () => {
    return pedido.reduce((total, item) => total + item.preco, 0).toFixed(2);
  };

  const finalizarPedido = async () => {
    if (!mesa || pedido.length === 0) {
      alert("Por favor, selecione a mesa e adicione itens ao pedido!");
      return;
    }

    const pedidoData = {
      mesa,
      itens: pedido,
      total: calcularTotal(),
    };

    try {
      const response = await fetch("/pages/api/pedido", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(pedidoData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Erro ao finalizar pedido.");
      }

      setShowAlert(true);
      setMesa("");
      setPedido([]);
    } catch (error) {
      console.error("Erro ao finalizar pedido:", error);
      alert("Erro ao finalizar pedido. Tente novamente.");
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      {/* Pedido */}
      <div className="mt-8 max-w-md mx-auto">
        <h2 className="text-2xl font-semibold mb-4">Seu Pedido</h2>
        {pedido.length > 0 ? (
          <>
            <ul>
              {pedido.map((item, index) => (
                <li key={index} className="flex justify-between">
                  <span>{item.nomeProduto}</span>
                  <span>R$ {item.preco.toFixed(2)}</span>
                </li>
              ))}
            </ul>
            <div className="mt-4 flex justify-between">
              <span>Total:</span>
              <span>R$ {calcularTotal()}</span>
            </div>
            <Button onClick={finalizarPedido} className="w-full mt-4 bg-green-500 text-white py-3 rounded-lg">
              Finalizar Pedido
            </Button>
          </>
        ) : (
          <p>Nenhum item no pedido.</p>
        )}
      </div>

      {/* Alerta de Pedido Finalizado */}
      <AlertDialog open={showAlert} onOpenChange={setShowAlert}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Pedido Finalizado!</AlertDialogTitle>
            <AlertDialogDescription>
              Seu pedido para a mesa {mesa} foi finalizado com sucesso.
              Total: R$ {calcularTotal()}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <Button onClick={() => setShowAlert(false)}>Fechar</Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
};

export default MenuPage;
